/* --------------------------------------------------------------
 * = 图文列表
 * ----------------------------------------------------------- */


.media {
    padding: 10px 0;
    border-bottom: 1px dotted @border-color;
}

.media-last{
    border-bottom: 0;
}

li.media:last-of-type {
    padding: 10px 0;
    border-bottom: none;
}


// 媒体图像，默认图片在左边
.media-cap {
    display: block;
    float: left;
    overflow: hidden;
    width: 64px;
    margin-top: 5px;
    margin-right: 10px;
    i{
        font-size:48px;
        text-align:center;
        line-height:64px;
    }
}


// 媒体文字行高
.media-body {
    line-height: 1.75;
    a {
	    color: @text-color;
	}
}

// 媒体描述文字
.media-body-des {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

// 媒体标签
.media-label {
    line-height: 1;
    display: inline-block;
    overflow: hidden;
    width: 100px;
    white-space: nowrap;
    text-overflow: ellipsis;
}

// 媒体标题
.media-body-title{
	a {
	    color: @text-color;
	    &:hover{
	    	color: @link-color;
	    }
	    &:visited{
	    	color: @text-light;
	    }
	}

}


// 图片在上面
.media-top{
    .media-cap{
        margin-top: 0;
        margin-right: 0;
        margin-bottom: 10px;
        width: 100%;
        height: auto;
    }
}

// 图片在右边
.media-right{
    .media-cap{
        float: right;
        margin-left: 10px;
        margin-right: 0;
    }
}


// 鼠标滑过显示内容
.media-hover {
    padding: 0;
    border: 0;
    .media-body{
        color: #fff;
    }
    .media-cap{
        margin: 0;
        padding: 0;
        float: none;
    }
    .media-body-title{
        color: #fff;
        white-space: nowrap;
        padding: 5px;
        margin-top: -34px;
        background: #333;
        background: rgba(0,0,0, .5);
        position: relative;
        z-index: 999;
        text-overflow: ellipsis;
        overflow: hidden;
        strong,a{
            color: #fff;
        }
    }
}


// 媒体盒子，测试中，看是否有必要使用，可以和box结合
.media-box {
    min-height: 64px;
    padding: 0;
    border: 1px solid @border-color;
    _height: 64px;
    _zoom: 1;
}

.media-box .media-cap {
    height: auto;
    margin-right: 10px;
    text-align: center;
    color: #fff;
    border: none;
    background: @background;
}

.media-box .media-cap i {
    font-size: 24px;
    line-height: 64px;
    text-align: center;
}

.media-box .media-cap .icon-envelope {
    line-height: 80px;
    height: 80px;
}

.media-box .media-body {
    padding: 10px 10px 10px 0;
}
