/* --------------------------------------------------------------
 * = 评论样式
 * ----------------------------------------------------------- */

.comment-list {
  &:extend(.list-unstyled all);
  margin-bottom: 40px ;
  margin-left: 0px ;
  padding-left: 0;
  .children {
    margin-left: 0px;
    padding-left: 40px;
    border-left: 1px solid #dedede;
    background-repeat: no-repeat;
    background-position: left 20px;
  }
  li.comment > div img.avatar {
    position: absolute;
    left: 29px;
    top: 29px;
  }
  .avatar{
    border-radius: 3px;
    border: 1px solid #ddd;
  }
  ol {
    list-style: none;
  }
}


// 当前登录
p.logged-in-as {
  padding-bottom: 0px;
}

.comment .comment-body {
  margin: 0;
  overflow: hidden;
  padding: 25px;
  margin-top: 2em;
  padding-left: 80px;
  position: relative;
  border: 1px solid #EDEDED;
  background: #FBFCFD;
  border-radius: 3px;
}

.comment-body{
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.comment-content{
  margin-top: 10px;
}

#comment-form-title {
  font-size: 24px;
  padding-bottom: 10px;
}

.comment-author.vcard .says{
  padding-left: 2px;
}

// 评论头像
.vcard .avatar {
  position: absolute;
  left: 15px;
  top: 29px;
  border-radius: 2px;
}

.comment-metadata {
  font-size: 11px;
  line-height: 16px;
  margin-bottom: 10px;
  a{
    color: @text-lighter;
    font-size: 12px;
  }
}


// 评论导航
.comment-navigation, .single .nav-links {
  overflow: hidden;
  margin-top: 30px;
}



// 评论表单，通过extend的方式应用框架的样式
.comment-form{
  &:extend(.pure-form all);
  &:extend(.pure-form-stacked all);
  font-size: 12px;
}

.comment-form p {
  &:extend(.pure-control-group all);
}

.comment-form input[type="text"],
.comment-form input[type="email"],
.comment-form input[type="url"]{
  width: 75%;
  @media (max-width: 48em){
    width: 100%;
  }
}
.comment-form textarea {
  width: 75%;
  height: auto;
  line-height: 1.5;
  @media (max-width: 48em){
    width: 100%;
  }
}
.comment-form input[type="submit"] {
  &:extend(.pure-button all);
  &:extend(.pure-button-primary all);
  font-size: 14px;
  padding: 8px 15px;
  margin-top: 15px;
}
.comment-reply-link{
  color: #bbb;
}

.search-form{
  &:extend(.pure-form all);
  .search-field{
    font-size: 14px;
    line-height: 1.6;
  }
}

.search-submit{
  &:extend(.pure-button all);
}