/* --------------------------------------------------------------
 * = 弹出框
 * ----------------------------------------------------------- */

.modal {
  position: absolute;
  z-index: 1002;
  box-shadow: 1px 1px 2px 2px #888;
  background: #fff;
  width: 600px;
  _overflow: hidden;
  &-open {
    overflow: hidden;
    overflow-y: scroll;
  }
  &-title {
    height: 42px;
    line-height: 42px;
    padding: 0 20px;
    font-size: 16px;
    border-bottom: 1px solid #ddd;
  }
  &-content {
    padding: 15px 20px;
  }
  .close {
    position: absolute;
    top: 10px;
    right: 20px;
  }
  &-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(18,18,18,0.3);
    z-index: 1001;
  }
  .button {
    margin-right: 5px;
  }
  &-load .modal-content {
    background:url(http://s.baixing.net/img/style/loading.gif) no-repeat center center;
  }
}
