// 文章标题样式
.entry-header{
     margin: 1em 0 2em;
    .entry-title{
        font-size: 28px;
        text-align: center;
        padding-bottom: 5px;
        border-bottom: 1px solid #dedede;
    }
    .entry-meta{
        text-align: center;
        padding: 8px 0;
        font-size: 12px;
        color: #999;
        a{
            color: #999;
        }
    }
}


// 文章内容样式
.entry-content{
    &:extend(.typo all);
    font-size: 16px;
    img{
        max-width: 100%;
        height: auto;
    }
}


// WP 默认分页导航样式
.nav-links {
    margin-top: 15px;
    margin-bottom: 30px;
    clear: both;
    font-size: 14px;
    line-height: 1.8;
    overflow: hidden;
    a{
        color: #666;
    }
}
.nav-previous, .previous-image {
	display: block;
    width: 100%;
}
.nav-next, .next-image {
	display: block;
    width: 100%;
}

.meta-nav{
	float: left;
	padding-right: 5px;
	&:after{
		content: "：";
	}
}

.post-password-form{
  &:extend(.pure-form all);
  input{
    font-size: 12px;
  }
  input[type=submit]{
    &:extend(.pure-button all);
  }
}