/* --------------------------------------------------------------
 * = 简码样式
 * ----------------------------------------------------------- */

// toggle 和 accordion样式
.toggle,
.accordion {
	border-bottom: 1px solid @border-color;
	dt, dd {
		border: 1px solid @border-color;
		border-bottom: 0;
		a {
			padding: 10px;
			display: block;
			color: black;
			font-weight: bold;
		}
	}
	dd {
		margin: 0;
		padding: 10px;
		border-top: 0;
		font-size: 12px;
	}
}

.tab-contents{
	padding: 15px;
	border: 1px solid #eee;
	border-top: 0;
}