/* --------------------------------------------------------------
 * = 布局，侧边栏样式
 * ----------------------------------------------------------- */

// 小工具统一样式
.widget-area{
	margin-right: 20px;
	@media (max-width: 48em) {
		margin-right: 0;
		.sjiameng{
			display: none;
		}
	}
  
  // 侧边栏小工具样式
  .widget{
      background: #f8f8f8;
      border: 1px solid #d8d8d8;
      border-top: 0;
      margin: 0 0 20px 0;
      @media (max-width: 48em) {
          margin: 0 0 20px 0;
      }
      ul{
          margin: 0;
          padding: 0;
          list-style: none;
          img{
              padding-bottom: 8px;
              vertical-align: middle;
          }
      }
      a{
          font-size: 14px;
      }
  }


  // 小工具标题
  .widget-title{
      background: @maincolor;
      color: #fff;
      padding: 5px 15px;
      margin: 0; 
      font-size: 18px;
      font-weight: 400;
      height: 36px;
      line-height: 36px;
      text-align: center;
  }

}


// 标签云样式
.tagcloud{
    padding: 20px 15px;
    a{
        .pure-u;
        margin: 3px 0;
        padding: 3px 5px;
        color: #fff;
        background: #f60; 
    }
}

// 联系方式小工具样式
.widget_contact{
    background: @background url(../images/ctbg.png) left bottom no-repeat;
    .ct-info{
        padding: 10px 15px;
        h2{
            font-size: 16px;
            font-weight: 400;
        }
        p{
            line-height: 1.8;
        }
    }
}

.widget_category_tree{
    .widget-title{
        border-bottom:0;
    }
}

.wizhi-category-tree {
	.pure-menu-list{
		margin: 0;
		padding: 0;
	}
	.pure-menu-link{
		padding: 10px;
		border-top: 1px solid darken(@background, 10%);
		border-bottom: 0;
		color: #333;
		background: #f8f8f8;
		font-size: 14px;
		display: block;
		&:hover{
			color: #fff;
			background: #436bb8;
		}
	}

  .pure-menu-active{
    &>.pure-menu-children{
      display: block;
    }
  }

  // 二级子菜单样式
	.pure-menu-children {
		margin: 0;
		padding: 0;
		display: none;
		border: 1px solid darken(@background, 10%);
		border-top: 0;
		.pure-menu-item{
			position: relative;
			.pure-menu-link {
				color: #333;
				font-size: 14px;
				font-weight: 400;
				padding: 10px;
				background: @background;
				&:hover{
					color: @highlight;
					background: @maincolor;
				}
			}
		}

    // 三级子菜单样式
		.pure-menu-children{
			.pure-menu-link{
				background: @background;
			}
		}

    // 子菜单当前样式
		.current-cat>a {
			color: #fff;
			background: #436bb8;
		}
	}
}


//多级accordion菜单
.widget_nav_menu .menu {
	list-style: none;
	padding: 0 15px !important;
	li {
		position: relative;
		text-align: center;
		a {
			color: @text-color;
			line-height: normal;
			padding: 15px;
			display: block;
			border-bottom: 1px solid @border-color;
			&:hover {
				color: @maincolor;
			}
		}
		// 当前菜单高亮
		&.current_page_item,
		&.current-menu-item,
		&.current-cat{
			& > span{
				color: @highlight;
			}
			& > a{
				color: @maincolor;
			}
			&:before{
				color: @maincolor;
			}
		}
		&:last-child{
			a{
				border: 0;
			}
		}
		&:before{
			content: ">";
			color: #bbb;
			display: inline;
		    float: left;
		    height: 48px;
		    line-height: 48px;
		    padding-left: 15px;
		    font-size: 16px;
		}
	}

	// 二级菜单样式
	ul {
		display: none;
		li {
			clear: both;
			a {
				padding-left: 2em;
				font-weight: normal;
				outline: 0;
				&:hover {
					background-color: @maincolor;
					color: @highlight;
				}
			}
		}

		// 三级菜单样式
		ul li a {
			padding-left: 4em;
			&:hover {
				background-color: @maincolor;
				color: @highlight;
			}
		}
	}

	// 菜单切换样式
	span {
		color: @text-light;
		font-size: 18px;
		line-height: normal;
		padding: 6px 15px;
		border-left: 1px solid @border-color;
		cursor: pointer;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 99;
	}
}


// 小工具菜浮动单样式
.widget{
	.wizhi-menu-list{
		padding: 10px;
		list-style: none;
		li {
			position: relative;
			a {
				line-height: normal;
				padding: 3px 10px;
				display: block;
				&:hover {
					color: @maincolor;
				}
			}
			// 当前菜单高亮
			&.current-menu-item,
			&.current-cat{
				& > a{
					color: @maincolor;
				}
			}
		}
		.children{
			a:before{
				content: "\25B8 ";
				font-size: small;
				color: @text-light;
				padding-right: 5px;
			}
		}
	}
}


// 自定义分类法下拉
.taxonomy_dropdown{
	padding: 5px 10px;
	select{
		display: block;
		width: 100%;
		height: 37px;
		padding: 6px 12px;
		font-size: 14px;
		line-height: 1.7;
		color: #555;
		background-color: #fff;
		background-image: none;
		border: 1px solid #ccc;
		border-radius: 4px;
	}
}