/* --------------------------------------------------------------
 * = 布局-头部样式
 * ----------------------------------------------------------- */

// 菜单变量
@menu-text: #333;
@menu-text-hover: #fff;
@menu-background-hover: #0078e7;

@mobile-dropdown-text: #fff;
@mobile-dropdown-bg: #333;
@mobile-dropdown-border: #363636;
@mobile-dropdown-bg-hover: #000;
@mobile-dropdown-border: #222;

@mobile-menu-height: 48px;

// 顶部链接栏
.topbar{
    background: @background;
    border-bottom: 1px solid darken(@background, 6fv%);
    a{
        color: #666;
        font-size: 12px;
    }
}

// 网站头部
.site-header {
    //border-bottom: 1px solid darken(@maincolor, 8%);
    position: relative;
    z-index: 999;
    h1{
        font-size: 18px;
        margin:0;
    }
    @media (max-width: 48em){
        margin-top: 0;
        border-bottom: 0;
        h1{
            font-size: 30px;
        }
    }
}

.head-right{
    padding-top:35px;
}


// 网站标题
.site-title {
  margin: 10px 0;
  @media (max-width: 48em){
    margin: 20px 0;
    text-align: center;
    img{
        max-width: 80%;
    }
  }
}


// 侧边栏菜单
.sidebar-wrapper {
    background: #333;
    position: relative;
    height: 100%;
    overflow: auto;
}


// 移动端的垂直菜单
.pure-menu-vertical{
    .pure-menu-active{
        .pure-menu-children {
            width: 100%;
            top:100%;
            left: 0;
        }
    }

}


// 主导航菜单样式
.main-navigation{
    line-height: 1.6;
    @media (max-width: 48em) {
        margin-top: 0;
    }
    // 一级导航菜单
    .pure-menu-list{
        li{
            position: relative;
            @media (max-width: 48em) {
                width: 100%;
            }
            a{
                // .pure-u;
                color: @menu-text;
                font-size: 16px;
                display: block;
                padding: 8px 20px;
                text-decoration: none;
                @media (max-width: 48em) {
                  color: @mobile-dropdown-text;
                  padding: 8px 14px;
                  border-bottom: 1px solid @mobile-dropdown-border;
                  background: @mobile-dropdown-bg;
                }
            }
        }
        @media (max-width: 48em) {
            display: none;
            margin-top: 0;
        }
    }

    // 鼠标经过样式
    .pure-menu-selected,
    .pure-menu-link:hover,
    .pure-menu-link:focus{
        color: @menu-text-hover;
        background: @menu-background-hover;
        @media(max-width: 48em) {
          color: @mobile-dropdown-text;
          background: @mobile-dropdown-bg-hover;
        }
    }

    // 一级导航中当前选中菜单样式
    .current-menu-ancestor .pure-menu-link,
    .pure-menu-selected .pure-menu-link{
        color: @menu-text-hover;
        @media (max-width: 48em) {
          color: @mobile-dropdown-text;
          border-bottom: 1px solid @mobile-dropdown-border;
          background: @mobile-dropdown-bg-hover;
        }
    }

    // 下拉二级菜单样式
    .pure-menu-children{
        margin-top: -1px;
        min-width: 120px;
        background: @mobile-dropdown-bg;
        box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2);
        @media (max-width: 48em) {
            position: relative;
            color: @mobile-dropdown-text;
            background: @mobile-dropdown-bg;
            box-shadow: none;
            margin-top: 0;
            border-top: 0;
        }

        // 下拉菜单中的当前菜单样式
        .pure-menu-selected{
            &>a{
                color: @menu-text;
                background: @mobile-dropdown-bg-hover; 
                @media (max-width: 48em) {
                    color: @mobile-dropdown-text;
                    background: @mobile-dropdown-border;
                }
            }
        }
        
        // 下拉菜单链接样式
        .pure-menu-link{
            color: @mobile-dropdown-text;;
            padding: 10px 15px;
            border-bottom: 1px solid @mobile-dropdown-border;
            @media (max-width: 48em) {
              color: @mobile-dropdown-text;
              border-bottom: 1px solid @mobile-dropdown-border;
              background: @mobile-dropdown-bg;
              text-indent: 2em;
            }
        }

        // 下拉菜单中的鼠标经过样式
        .pure-menu-link:hover,
        .pure-menu-link:focus{
            background: @mobile-dropdown-bg-hover;
            @media (max-width: 48em) {
              color: @mobile-dropdown-text;
              background: @mobile-dropdown-bg-hover;
            }
        }

        .pure-menu-has-children > .pure-menu-link::after {
          content: "\25B8" !important;
        }

        // 下拉第三级下拉菜单
        .pure-menu-children{
            top: 0;
            left: 100%;
            @media (max-width: 48em) {
                top: 100%;
                left: 0;
                .pure-menu-link{
                   text-indent: 4em; 
                }
            }
        }

    }
}


// 移出默认的链接颜色
.pure-menu-active > .pure-menu-link{
    background: transparent;
}


// 调整下拉菜单箭头的大小
.pure-menu-has-children>.pure-menu-link:after{
    font-size: medium;
    line-height: 1;
}


// 移动端样式
@media (max-width: 48em) {
    .pure-menu-horizontal{
        white-space: normal !important;
    }
    .pure-menu-list{
        li{
            width: 100%;
        }
    }
}


// 移动导航样式
.ui-navbar{
    position: relative;
    @media(max-width:48em){
        // 菜单切换小图标
        background: @maincolor;
        height: @mobile-menu-height;
        .ui-navbar-nav{
            width: 100%;
        }
        #btn-so{ 
            float:right; 
            margin:10px 0; 
            width:40px; 
            height:36px; 
            background:url(../images/search.png) no-repeat center 12px; 
            display:inline-block; 
            cursor:pointer;
        }
        #search-box{
            top: 57px;
        }
    }
    
}

// 菜单图标
.ui-navbar-toggle{
    display: none;
    width: 54px;
    height: @mobile-menu-height;
    color: #fff;
    font-size: 28px;
    text-align: center;
}


.nav-contact{
    font-size: 18px;
    height: @mobile-menu-height;
    line-height: @mobile-menu-height;
    color: #fff;
    padding-right: 15px;
}


// 搜索按钮
#btn-so{ 
    float:right; 
    margin-top:2px; 
    width:40px; 
    height:36px; 
    background:url(../images/search.png) no-repeat center 12px; 
    display:inline-block; 
    cursor:pointer;
}


// 搜索框
#search-box{
    width:400px;
    background:#FFF;
    display:none;
    position:absolute;
    right:0;
    text-align: left;
    top:49px;
    z-index:100;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
    @media (max-width: 48em) {
        width:100%;
    }
    #searchform{
        padding:10px;
        background:#FFF;
    }
    #ls{
        padding:10px 3%;
        width:78%;
        border:1px solid #DDD;
        border-right:none;
        color:#666;
    }
    #searchsubmit{
        padding:11px 0;
        width:14%;
        text-indent:-9999px;
        border:none; background:#666 url(../images/search.png) no-repeat center -32px;
        cursor:pointer;
        osition:absolute;
        right:10px;
        top:10px;
        &:hover{
           background-color:@maincolor; 
        };
    }
}