@import "../_variables";

/* --------------------------------------------------------------
 * = 列表样式
 * ----------------------------------------------------------- */


// 带背景的列表，一半作为菜单使用
.list {
    color: @text-color;
    border-top: none;
    background: #fff;
}

// 列表元素
.list-item {
    position: relative;
    width: 100%;
    a {
	    line-height: 1.1;
	    display: block;
	    padding: 10px 15px;
	    color: @text-color;
	    *zoom: 1;
        border: 1px solid @border-color;
        margin-bottom: -1px;
	}
    &:first-child{
        a{
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
        }
        
    }
    &:last-child{
        a{
            margin-bottom: 0;
            border-bottom-right-radius: 4px;
            border-bottom-left-radius: 4px;
        }
    }
}

// 鼠标滑过和激活状态
.list-item a:hover,
.list .active a {
    text-decoration: none;
    color: #fff;
    background: @maincolor;
}


// 小徽章
.list-item-badge {
    font-size: 12px;
    line-height: 16px;
    position: absolute;
    top: 11px;
    right: 15px;
    display: inline-block;
    height: 16px;
    padding: 0 6px;
    pointer-events: none;
    color: #fff;
    border-radius: 8px;
    background: @maincolor;
}

// 激活状态的小徽章
.list-item:hover .list-item-badge,
.list .active .list-item-badge {
    color: @maincolor;
    background: #fff;
}


// 链接列表，常见于新闻标题链接

// <ul class="ui-list ui-list-gray">
//     <li class="ui-list-item"><a href="#">如何申请认证？</a></li>
//     <li class="ui-list-item"><a href="#">如何提现？</a></li>
//     <li class="ui-list-item"><a href="#">支付宝数字证书有什么作用？</a></li>
//     <li class="ui-list-item"><a href="#">如何申请认证？</a></li>
// </ul>

.ui-list {
    margin: 0;
    padding: 10px;
    list-style: square inside;
}

/* 默认有方角 */
.ui-list-item {
    line-height: 20px;
}
.ui-list-item a {
    line-height: 20px;
    text-decoration: none;
    color: @link-color;    
}

.ui-list-item a,
.ui-list-item span.ui-list-item-text {
    vertical-align: middle;    
}

.ui-list-item a:hover {
    text-decoration: underline;    
}

/* 灰色小方角 */
.ui-list-gray .ui-list-item {
    color: @text-color;
}

/* 灰色小方角＋灰色链接 */
.ui-list-graylink .ui-list-item {
    color: @text-color;    
}

.ui-list-graylink .ui-list-item a {
    color: @text-lighter;
}

/* 没有小图标的 */
.ui-list-nosquare {
    list-style: none;    
}

