/* --------------------------------------------------------------
 * = 布局-页面样式
 * ----------------------------------------------------------- */

.entry-content{
  .widget{
      @media (max-width: 760px) {
          display: none;
      }
      ul{
          list-style: none;
          img{
              padding-bottom: 8px;
              vertical-align: middle;
          }
      }
      a{
          font-size: 14px;
      }
  }


  // 小工具标题
  .widget-title{
      background: #f2f2f2;
      padding: 5px 10px;
      margin: 0; 
      font-size: 18px;
      font-weight: 400;
  }

}

.page-template-page-full{
  .site-main{
    padding: 0;
  }
  .entry-header{
    display: none;
  }
}