/* --------------------------------------------------------------
 * = 布局-全局样式
 * ----------------------------------------------------------- */

body,
.pure-g [class*="pure-u"] {
  font-size: 14px;
  font-family: 'Microsoft YaHei', 'Helvetica Neue', arial, sans-serif, 'Hiragino Sans GB', Tahoma;
}

a {
  text-decoration: none;
  color: @link-color;
  &:hover {
    color: @link-hover-color;
    text-decoration: none;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

td {
  border: 1px solid #ccc;
  padding: 5px 8px;
}

.site-content {
  background: #fff;
  padding-bottom: 20px;
  position: relative;
  z-index: 10;
  @media (max-width: 48em){
    padding: 10px;
  }
}

.home{
  .site-content{
    @media (max-width: 48em){
      padding: 0;
      .ly{
        padding-left: 15px;
        padding-right: 15px;
      }
    }
  }
}

.main-content {
  margin-top: 25px;
  width: 100%;
}

.post-type-archive-case,
.home{
  .main-content{
    margin-top: 0;
  }
}

.wrap {
  background: #fff;
  padding: 0 5px;
}

.w {
  max-width: @width;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.site-main{
  border: 1px solid @border-color;
  padding: 30px;
}