/* --------------------------------------------------------------
 * = 盒子
 * ----------------------------------------------------------- */

// 元素盒子
.ui-box {
    border: 1px solid @border-color;
    zoom: 1;
    margin: 0;
    padding: 0;
    border-bottom: none;
    &:after {
        clear: both;
        content: " ";
        display: block;
        font-size: 0;
        height: 0;
        visibility: hidden;
    }
}


.ui-box-shadow{
    box-shadow: 0 1px 2px 0px rgba(0,0,0, .2);
}


//使用在box和上面的box相连的情况下
.ui-box-follow{
    border-top: none;
}

.ui-box-head {
    border-bottom: 1px solid @border-color;
    position: relative;
    padding: 8px 10px;
    // background: linear-gradient(to top, #fcfcfc, #f9f9f9);
    zoom: 1;

    // 标题栏标题
    .ui-box-head-title {
        color: @text-color;
        font-size: 14px;
        font-weight: bold;
        display: inline-block;
        *display: inline;
        zoom: 1;
        margin: 0;
        padding: 0;
        line-height: normal;
    }

    // 标题栏标题后面的描述文字
    .ui-box-head-text {
        margin-left: 10px;
        color: @text-light;
    }

    // 标题栏右边的更多
    .ui-box-head-more {
        float: right;
    }
}


.ui-box-foot {
    border-bottom: 1px solid @border-color;
    position: relative;
    padding: 8px 10px;
    // background: linear-gradient(to top, #fcfcfc, #f9f9f9);
    zoom: 1;

    // 标题栏标题
    .ui-box-foot-left {
        color: @text-color;
        font-size: 14px;
        font-weight: bold;
        display: inline-block;
        *display: inline;
        zoom: 1;
        margin: 0;
        padding: 0;
        line-height: normal;
    }

    // 标题栏右边的更多
    .ui-box-foot-right {
        float: right;
    }
}

// 盒子内容容器
.ui-box-container {
    background: #fff;
    border-bottom: 1px solid @border-color;
}

//可选，用于默认有内边距的box
.ui-box-content{
    padding: 10px;
    .medias{
        margin-top: -10px;
        margin-bottom: -10px;
    }
    .ui-list{
        padding: 0;
    }
}