/* --------------------------------------------------------------
 * = 布局-页脚样式
 * ----------------------------------------------------------- */


.site-footer {
    color: #fff;
    line-height: 24px;
    padding: 15px 0 15px 0;
    margin-top: 100px;
    background: #353638;
    @media (max-width: 48em) {
        text-align: center;
    }
    a{
        color: #fff;
    }
}

.site-info{
    margin-top: 15px;
}

.footer-logo{
    height: 119px;
    margin-top: -74px;
    overflow: visible;
    position: relative;
    z-index: 999;
}

.menu-footer{
    padding: 8px 0;
    .menu-item{
        display: inline-block;
        padding-right: 5px;
    }
}

// 回顶部
.scroll-to-top {
    background: #333 url(../images/backtop.gif) no-repeat scroll center center;
    bottom: 10%;
    cursor: pointer;
    display: none;
    width: 48px;
    height: 44px;
    opacity: .4;
    position: fixed;
    right: 15px;
    z-index: 999;
}
.scroll-to-top:hover {
    opacity: .5;    
}


/* 客户服务 */
.cs-div{ 
    width:140px;
    height:auto;
    text-align:center;
    position:absolute;
    z-index:99999;
    overflow:hidden;
    #cs-close{ 
        float:right; 
        width:32px; 
        height:35px; 
        text-indent:-9999px; 
        display:block; cursor:pointer; 
        position:absolute; 
        top:0; 
        right:0;
    }
}
.cs-bar{ 
    padding:0; 
    width:32px; 
    height:116px;
    #cs-close{
        width:32px;
        height:116px;
        background:url(../images/cs.gif) no-repeat -106px 0; 
        top:0; right:0;
    }
}
.cs-inner{ 
    padding:60px 5px 0 10px; 
    background:url(../images/cs.gif) no-repeat 0 -120px; 
    display:none;
    img{ 
        margin-bottom:10px;
    }
}
.cs-btm{ width:100%; 
    height:15px; 
    background:url(../images/cs.gif) no-repeat 0 bottom;
}


.fright{
    h2{
        background: url(../images/phone.png) left center no-repeat;
        margin: 15px 0 0;
        padding: 0 0 0 15px;
        font-family: serif;
        font-style: italic;
        font-size: 28px;
        @media(max-width: 48em){
            background: none;
        }
    }
    p{
        font-size: 12px;
        margin: 5px 0;
    }
}

.flinks{
    @media(max-width: 48em){
        display: none;
    }
}